import React from "react";
import { Slide } from "@material-ui/core";
import $ from "jquery";

const useHideOnScroll = options => {
  const { threshold, scroller, id } = options;

  const [hide, setHide] = React.useState(false);

  let lastScrollPos = undefined;

  const handleScroll = React.useCallback(() => {

    var myEle = document.getElementById(id + "-bottom-side");
    if (myEle) {
      var scrollTop   = $(window).scrollTop(),
      elementOffset   = $('#' + id + "-bottom-side").offset().top,
      distance        = (elementOffset - scrollTop);
      if (lastScrollPos === undefined) {
        lastScrollPos = distance;
        return;
      }
      if (lastScrollPos > distance) {
        setHide(true);
      }
      else if (lastScrollPos < distance) {
        setHide(false);
      }
      lastScrollPos = distance;
    }
  }, [scroller, threshold, id]);

  React.useEffect(() => {
    window.addEventListener("scroll", handleScroll, true);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [handleScroll]);

  return hide;
};

export default function CollapsibleAppBar(props) {
  const { children, threshold, scroller, id, ...other } = props;

  const hide = useHideOnScroll({ threshold, scroller, id });

  return (
    <div>
      <Slide appear={false} direction="down" in={!hide} {...other}>
        {children}
      </Slide>
      <div id={id + "-bottom-side"} style={{width: "100%", height: 4}}/>
    </div>
  );
}
